























import {Component, Vue} from 'vue-property-decorator';
import Dygraph from 'dygraphs';
import {origins} from "@/origins";
import PerSeriesOptions = dygraphs.PerSeriesOptions;

@Component({})
export default class Home extends Vue {
  private graph: Dygraph | undefined;
  private lables = new Map<string, number>()
  private show = true
  private includeParents = true
  private nameFilter = ''
  private placeFilter = ''
  private theOrigins = new Map<string, string[]>()

  public mounted() {
    const series: {
      [seriesName: string]: PerSeriesOptions,
    } = {};
    this.graph = new Dygraph('graph', 'graph2.csv', {
      connectSeparatedPoints: true,
      strokeWidth: 0.5,
      highlightSeriesOpts: {
        strokeWidth: 1.5,
        strokeBorderWidth: 1,
        highlightCircleSize: 5,
      },
      labelsShowZeroValues: false,
      series,
      stepPlot: true,
      valueRange: [750, 2320]
    });
    for (let origin of origins.split("\n")) {
      const os = origin.split(",")
      this.theOrigins.set(os[0], os.slice(1))
    }
    this.graph.ready(() => {
      this.graph!!.getLabels().forEach((l, i) => {
        // if (i !== 0)
          this.lables.set(l, i - 1)
      });
    })
  }

  private refilter() {
    if (this.graph) {
      const sss = new Set<number>()
      const vs = this.graph.visibility()
      this.graph.getLabels().forEach((l, i) => {
        if (i !== 0 && l !== 'sd' && l !== 'teams') {
          const show = new RegExp(`^.*${this.nameFilter}.*\\(.*${this.placeFilter}.*\\)`, "i").test(l);
          if (show) {
            if (this.includeParents) {
              try {
                for (let string of this.theOrigins.get(l)!!) {
                  sss.add(this.lables.get(string)!!)
                }
              } catch (e) {
                console.log(l)
              }
            } else {
              sss.add(i - 1)
            }
          }
        }
      })
      const ss: number[] = [];
      const hs: number[] = [];

      vs.forEach((b, i) => {
        const bb = sss.has(i)
        if (b != bb) {
          (bb ? ss : hs).push(i);
        }
      })

      // @ts-ignore
      this.graph.setVisibility(ss, true);
      // @ts-ignore
      this.graph.setVisibility(hs, false);
    }
  }

}
